<template>
  <div class="container">
    <div class="card-columns">
      <div v-for="post in posts" :key="post.id" class="card">
        <img class="card-img-top" :src="post.urlToImage" alt="Card image cap">
        <div class="card-body">
          <h5 class="card-title">{{post.title}}</h5>
          <p class="card-text">{{post.description}}</p>
          <p class="card-text">
            <small class="text-muted">- {{post.author}}</small>
          </p>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "hello",
  data() {
    return {
      posts: null
    };
  },
  methods: {
    getData() {
      axios
        .get(
          `https://newsapi.org/v2/top-headlines?country=tr&apiKey=38570843a5d7483b8d932e9d827e3e05`
        )
        .then(response => {
          this.posts = response.data.articles;
        })
        .catch(e => {
          this.errors.push(e);
        });
    }
  },
  mounted() {
    this.getData();
  }
};
</script>

<style>
h1 {
  color: red;
}
</style>



